.post-container {
    /* Depending on design spec, I can always give the container a fixed width so it gives a consistent look regardless of the amount of text inside */
    max-width: 65rem;
    -webkit-box-shadow: 0 3px 3px rgba(0, 0, 0, 0.19), 0 3px 3px rgba(0, 0, 0, 0.23);
            box-shadow: 0 3px 3px rgba(0, 0, 0, 0.19), 0 3px 3px rgba(0, 0, 0, 0.23);
    border-radius: 0.2rem;
}

@media only screen and (max-width: 46.875em) {
    .post-container {
        position: relative;
    }
}



