.App {
    position: relative;
    height: 100%;
    width: 100%;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.game-background {
    background-color: rgba(21, 30, 27, 1);
    /* background-image: linear-gradient(to bottom, rgba(21, 30, 27, 0.75) 50%, rgba(21, 30, 27, 1) 70%), url(../src/assets/img/background.png); */
    background-image: -webkit-gradient(
            linear,
            left top, left bottom,
            from(rgba(31, 39, 48, 0.75)),
            to(rgba(31, 39, 48, 1))
        ),
        url(../src/assets/img/background.png);
    background-image: -webkit-linear-gradient(
            top,
            rgba(31, 39, 48, 0.75),
            rgba(31, 39, 48, 1)
        ),
        url(../src/assets/img/background.png);
    background-image: -o-linear-gradient(
            top,
            rgba(31, 39, 48, 0.75),
            rgba(31, 39, 48, 1)
        ),
        url(../src/assets/img/background.png);
    background-image: linear-gradient(
            to bottom,
            rgba(31, 39, 48, 0.75),
            rgba(31, 39, 48, 1)
        ),
        url(../src/assets/img/background.png);
    /* background-position: top; */
    /* background-attachment: fixed; */
    background-size: 100% auto;
    background-repeat: no-repeat;
}



